body{position:relative}body *{box-sizing:border-box}body,div,ul,li,dl,dd,dt,ol,h1,h2,h3,h4,h5,h6,input,fieldset,legend,p,select,table,th,td,tr,textarea,button,form,figure,figcaption{margin:0;padding:0}a,a:hover{text-decoration:none;color:#333}body,input,textarea,select,button,table{font-family:"Noto Sans KR",sans-serif;font-family:"Noto Sans",sans-serif}em,address{font-style:normal}em{color:red}ul,li,ol{list-style:none}h1,h2,h3,h4,h5,h6{font-size:1rem;font-weight:500;display:inline-block}button{border:0;background:none}img,video{width:100%}img{vertical-align:top}img{border:0}table{border-collapse:collapse}.hidden{position:absolute;left:-1000%;top:0;width:1px;height:1px;overflow:hidden;color:#fff}section{height:100vh;display:flex;justify-content:space-around;align-items:center;position:relative}#About{background:url(../etc/about_back.png) 50% 30% no-repeat;background-size:cover}#About div.profile{display:grid;grid-template-columns:auto 1fr;gap:25px;grid-column-gap:30px;align-items:center;position:relative}#About div.profile.profile-left{align-items:center}#About div.profile.profile-left button{position:absolute;bottom:0px;right:30%}#About div.profile div.imgContent{grid-column:1;grid-row:1;text-align:center}#About div.profile div.imgContent img.profileImg1{width:376px;height:500px;border-radius:100px;background:#fff;box-shadow:5px 5px 20px #888}#About div.profile div.imgContent img.profileImg2{width:100%;height:800px;border-radius:40% 30% 40% 30%/45% 70% 45% 70%}#About div.profile div.textContent{grid-column:2;grid-row:1;padding-left:30px;width:800px}#About div.profile div.textContent div.detailProfile{color:#fff;font-weight:600}#About div.profile div.textContent div.detailProfile div.name{position:relative;display:inline-block;font-size:2.5em;font-weight:500;margin-bottom:20px}#About div.profile div.textContent div.detailProfile div.name::before{content:"정호석";display:block;font-size:1.3rem;position:absolute;right:-20%;top:-10%;transform:translate(20%, 10%)}#About di