er{color:#3b3b3b}#About div.profile div.textContent div.detailProfile dl:nth-of-type(3) dd ul li svg{width:50px;height:50px;border-radius:50%}#About div.profile div.textContent div.detailProfile ul.popol{border-top:1px dashed #ccc;padding-top:5%;display:flex;align-items:center;justify-content:flex-start;gap:30px}#About div.profile div.textContent div.detailProfile ul.popol li{width:200px}#About div.profile div.textContent div.detailProfile ul.popol li a{font-size:1.2em;border:3px dashed #fcfcfc;text-align:center;padding:10px;line-height:2rem;font-weight:500;color:#999;transition:all .8s ease-in-out;display:flex;align-items:center;justify-content:center;gap:20px}#About div.profile div.textContent div.detailProfile ul.popol li a:hover{background:#333;backdrop-filter:.8;border-radius:40px;color:#fff}#About div.profile div.textContent .introShort{margin:10px 0;text-align:center;font-size:1.3rem;color:#edeeff;line-height:50px}#About div.profile div.textContent .introShort b{color:#00985c;font-size:1.65rem}#About div.profile div.textContent .introShort strong{color:#00985c;font-size:1.65rem}#About div.profile button{width:60px;height:60px;cursor:pointer;position:absolute;bottom:0;right:0}#About div.profile button span{display:inline-block;width:100%;height:100%}#About div.profile button span.preClick{background:url(../etc/open_door.png) 50% 50% no-repeat;background-size:contain}#About div.profile button span.nextClick{background:url(../etc/back_door.png) 50% 50% no-repeat;background-size:contain}