// 통합 리셋 css 사용
@use'./common.scss';

#Home{
  background: url(../etc/macbook1_2.png) 50% no-repeat;
  background-size: cover;
  div{
    &.home{
      p{
        font-size: 3.5em;
        letter-spacing: 2px;
        color: #f8f8f8;
        text-align: center;
        strong{
          background: linear-gradient(to bottom, transparent 50%, #4867e8d9 50%) 0 -7px;
        }
      }
    }
  }
  a {
    &.scrollDown {
      position: absolute;
      left: 50%;
      bottom: 70px;
      font-size: 20px;
      transform: translateX(-50%);
      font-style: italic;
      color: #fff;
      z-index: 3;
      cursor: pointer;
      .downIcon {
        display: inline-block;
        animation: scrollDown 0.7s infinite linear;
      }
    }
  
    @keyframes scrollDown {
      0% {
        transform: translateY(0);
        opacity: 1;
      }
      100% {
        transform: translateY(10px);
        opacity: 0;
      }
    }
  }
  
}
