body{
  // width: 1920px;
  position: relative;
  *{
    box-sizing:border-box;
   }
}

/* 여백 초기화 */
body,div,ul,li,dl,dd,dt,ol,h1,h2,h3,h4,h5,h6,input,fieldset,legend,p,select,
table,th,td,tr,textarea,button,form,figure,figcaption{margin:0;padding:0;}

/* a 링크 초기화 */
a, a:hover {text-decoration: none; color: #333;}
// a:hover {text-decoration: none;}

/* 폰트 초기화 */
body, input, textarea, select, button, table 
{
  font-family: 'Noto Sans KR', sans-serif;
  font-family: 'Noto Sans', sans-serif; 
}

/* 폰트 스타일 초기화 */
em,address{font-style: normal;}
em{color:red;}

/* 블릿기호 초기화 */
ul,li,ol {list-style: none;}

/* 제목 태그 초기화 */
h1,h2,h3,h4,h5,h6 {font-size:1rem; font-weight: 500; display:inline-block;}

/* 버튼초기화 */
button{border: 0; background: none;} 
 
/* 반응형 이미지 */
img, video{width:100%;}
img {vertical-align: top;}
img {border:0;} 

/* table */
table{border-collapse: collapse;}

// class hidden 설정
.hidden { position: absolute; left: -1000%; top: 0; width: 1px; height: 1px; overflow: hidden; color: #fff;}

// 공통 section 
section{
  height: 100vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
}
